import React, { useContext, useState } from "react";
import {
  CircularProgress,
  Divider,
  Grid,
  Switch,
  Typography,
} from "@mui/material";
import HeaderWrapper from "components/Wrapper/HeaderWrapper";
import { makeStyles } from "@mui/styles";
import { ReactComponent as PushNotificationIcon } from "assets/PushNotificationIcon.svg";
import { ReactComponent as MainNotificationIcon } from "assets/MainNotificationIcon.svg";
import { ReactComponent as WhatsappNotificationIcon } from "assets/WhatsappNotificationIcon.svg";
import ArrowForwardRoundedIcon from '@mui/icons-material/ArrowForwardRounded';
import { useNavigate } from "react-router";
import { useAIEventHandler, useInstance } from "easy-redux-http-call";
import { AI_update_user_detail, AI_user_detail } from "API/userAPI";
import { Context } from "GlobalState";
import { getToken, messaging } from "../../firebase";

const useStyles = makeStyles((theme) => ({
  dmProfileRoot: {
    height: "100%",
    padding: theme.spacing(1),
    paddingTop: theme.spacing(2),
    "&>div": {
      marginTop: 10,
    },
  },
  dmNotificationContainer: {
    "&>div": {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      padding: "4px 0px",
      "&>div": {
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        "&>svg": {
          margin: "0px 6px 0px 4px ",
          width: 20,
          height: 20,
        },
      },
    },
  },
}));


export default function Setting() {
  const classes = useStyles();
  const navigate = useNavigate()

  const [loading, setLoading] = useState(false)

  const userState = useInstance(AI_user_detail)

  useAIEventHandler(AI_update_user_detail, {
    onSuccess: () => {
      setLoading(false)
    },
    onError: () => {
      setLoading(false)
    }
  })

  const redirectTo = (location) => () => {
    navigate(`/setting/${location}`)
  }

  const { darkMode, setDarkMode } = useContext(Context);

  const handleDarkMode = () => {
    let darkModeActive = window.localStorage.getItem("darkMode") || "light";
    window.localStorage.setItem(
      "darkMode",
      darkModeActive === "light" ? "dark" : "light"
    );
    setDarkMode(darkModeActive === "light" ? "dark" : "light");
  };

  const handleEnableNotification = () => {
    let enabled = userState?.data?.user?.notification?.enabled;

    AI_update_user_detail.call({
      params: {
        notification: { enabled: !enabled }
      }
    });
  }

  const handlePushNotification = async (e) => {
    if (loading) return false

    setLoading(true)
    let pushNotification = userState?.data?.user?.notification?.pushNotification;

    if (pushNotification) {
      AI_update_user_detail.call({
        params: {
          notification: { pushNotification: !pushNotification }
        }
      });
    } else {
      if (Notification.permission !== "granted") {
        const permission = await Notification.requestPermission();

        if (permission !== "granted") {
          setLoading(false)

          alert("Notification permission is required to enable push notifications.");
          return;
        }
      }

      try {
        const token = await getToken(messaging, {
          vapidKey: process.env.REACT_APP_FIREBASE_VAPI
        });

        AI_update_user_detail.call({
          params: {
            fcmToken: token,
            notification: {
              pushNotification: !pushNotification,
            },
          }
        });
        // You can send this token to your server to save it for future notifications
      } catch (error) {
        console.error("Error getting FCM token:", error);
        return;
      }
    }
  }

  const isNotificationEnabled = !!userState?.data?.user?.notification?.enabled;

  return (
    <HeaderWrapper>
      <Grid className={classes.dmProfileRoot}>
        <Typography variant="h5">Setting</Typography>
        <Grid className={classes.dmNotificationContainer}>
          <Grid onClick={redirectTo("account")}>
            <Grid>
              <Typography semiBold variant="subtitle1">
                Account
              </Typography>
            </Grid>
            <ArrowForwardRoundedIcon />
          </Grid>
          <Divider />

          <Grid onClick={redirectTo("group")}>
            <Grid>
              <Typography semiBold variant="subtitle1">
                Group
              </Typography>
            </Grid>
            <ArrowForwardRoundedIcon />
          </Grid>
          <Divider />
          <Grid onClick={redirectTo("users")}>
            <Grid>
              <Typography semiBold variant="subtitle1">
                Event Users
              </Typography>
            </Grid>
            <ArrowForwardRoundedIcon />
          </Grid>
          <Divider />
        </Grid>

        <Grid sx={{ mt: '26px !important' }}>
          <Typography grey variant="body2">
            Notification
          </Typography>
          <Divider />
          <Grid className={classes.dmNotificationContainer}>
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Reminder
                </Typography>
              </Grid>
              <Switch
                checked={!!userState?.data?.user?.notification?.enabled}
                onChange={handleEnableNotification}
              />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <PushNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Push notification
                </Typography>
              </Grid>
              <Grid display={"flex"} alignItems={"center"} gap={1}>
                {loading && <CircularProgress size={20} />}
                <Switch
                  disabled={!isNotificationEnabled}
                  checked={!!userState?.data?.user?.notification?.pushNotification}
                  onChange={handlePushNotification}
                />
              </Grid>
            </Grid>
            <Divider />
            <Grid className="comming-soon">
              <Grid>
                <MainNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Mail
                </Typography>
              </Grid>
              <Switch disabled={!isNotificationEnabled} />
            </Grid>
            <Divider />
            <Grid className="comming-soon">
              <Grid>
                <WhatsappNotificationIcon />
                <Typography semiBold variant="subtitle1">
                  Whatsapp
                </Typography>
              </Grid>
              <Switch
                disabled={!isNotificationEnabled}
                checked={userState?.data?.user?.notification?.whatsApp} />
            </Grid>
            <Divider />
          </Grid>
        </Grid>

        <Grid sx={{ mt: '26px !important' }}>
          <Typography grey variant="body2">
            Other thing
          </Typography>
          <Divider />
          <Grid className={classes.dmNotificationContainer}>
            <Grid className="comming-soon">
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Remind me for events
                </Typography>
              </Grid>
              <Switch />
            </Grid>
            <Divider />
            <Grid>
              <Grid>
                <Typography semiBold variant="subtitle1">
                  Dark mode
                </Typography>
              </Grid>
              <Switch
                defaultChecked={darkMode === "dark"}
                checked={darkMode === "dark"}
                onClick={handleDarkMode}
                value={darkMode}
              />
            </Grid>
            <Divider />
          </Grid>
        </Grid>
      </Grid>
    </HeaderWrapper>
  );
}
